ackground: $pf-white;\n        }\n      }\n\n      &.hamb-open {\n        .hamb-line {\n          background: transparent;\n        }\n      }\n    }\n  }\n\n  &__buttons {\n    display: flex;\n    align-items: center;\n\n    @include media-breakpoint('lg') {\n      margin-left: 1rem;\n      height: 100%;\n    }\n  }\n}","@mixin full-bleed {\n    margin-left: calc((100vw - 100%) / -2);\n    width: 100vw;\n}",".footer {\n    color: $pf-charcoal;\n\n    &__top {\n        background-color: $pf-off-white;\n\n        &__wrapper {\n            display: flex;\n            flex-direction: column;\n            gap: $spacing--md;\n\n            @include media-breakpoint(lg) {\n                flex-direction: row;\n            }\n        }\n\n        &__col1 {\n            display: flex;\n            flex-direction: column;\n            gap: $spacing--md;\n            padding: $spacing--md 0;\n\n            @include media-breakpoint(lg) {\n                flex-basis: 50%;\n            }\n\n        }\n\n        &__logo {\n            p {\n                display: inline !important;\n            }\n        }\n\n        &__nav {\n            @include media-breakpoint(md) {\n                margin-left: 3.5rem;\n            }\n\n            ul {\n                display: flex;\n                flex-wrap: wrap;\n                gap: 1rem;\n\n                li {\n                    width: calc(50% - 1rem);\n\n                    a {\n                        &:hover {\n                            text-decoration: underline;\n                        }\n                    }\n                }\n            }\n        }\n\n        &__col2 {\n            @include media-breakpoint(lg) {\n                flex-basis: 50%;\n            }\n        }\n\n        &__icons {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            flex-wrap: wrap;\n            gap: 1rem;\n\n            &__img {\n                width: 250px;\n             